import Canvas from "../../components/Canvas";
import './Map.scss'

function GuideMap({ itemModal, srcImage, setTypeModal, ...props }) {
    console.log(itemModal);
    return (
        <div className='modal_map_intro container-sm border'>
            <div className="row">

                <div className="col-sm-12">
                    <i className="bi bi-x-circle" style={{ float: 'right' }} onClick={() => {
                        setTypeModal(false);
                    }}></i>
                </div>
                <div className="col-sm-12">
                    <div className="row">
                        <div className="col-sm-3" style={{ margin: 'auto', fontSize: '18px !important', color: '#c66666' }}>
                            <span className='text_modal_map' style={{  fontSize: '20px'}}>
                                M t
                            </span>
                        </div>

                        <div className="col-sm-11" style={{ margin: 'auto' }}>
                            <span className='text_modal_map'>
                                y l bn  dn ti kho bu Rng. C rt nhiu chng ngi vt, th thch kh khn trn ng i tm kho bu. Mt s chng ngi vt c th cn tr bn hoc c th gip bn n kho bu nhanh hn. Di y l mt vi ch thch:
                            </span>
                        </div>

                    </div>
                </div>

                <div className="col-sm-12 border_map_modal animate__zoomInDown animate__animated" style={{marginTop: '20px'}}>
                    <div className="row" style={{ alignItems: 'center' }}>
                        <div className="col-sm-1">
                            <img
                                src={process.env.PUBLIC_URL + '/assets/images/khobau.png'}
                                style={{ width: '32px', height: '32px' }}
                            />
                        </div>
                        <div className="col-sm-11">
                            <span className='text_modal_map'>y chnh l kho bu Rng. Nu ly c n bn s c ban thng mt phn thng</span>
                        </div>
                    </div>
                </div>

                <div className="col-sm-12 border_map_modal animate__zoomInDown animate__animated">
                    <div className="row" style={{ alignItems: 'center' }}>
                        <div className="col-sm-1">
                            <img
                                src={process.env.PUBLIC_URL + '/assets/images/dice.png'}
                                style={{ width: '32px', height: '32px' }}
                            />
                        </div>
                        <div className="col-sm-11">
                            <span className='text_modal_map'>n vo y  c th di chuyn </span>
                        </div>
                    </div>
                </div>

                <div className="col-sm-12 border_map_modal animate__zoomInDown animate__animated">
                    <div className="row" style={{ alignItems: 'center' }}>
                        <div className="col-sm-1">
                            <img
                                src={process.env.PUBLIC_URL + '/assets/images/heart.png'}
                                style={{ width: '32px', height: '32px' }}
                            />
                        </div>
                        <div className="col-sm-11">
                            <span className='text_modal_map'>S lt chi ca bn l gii hn nn hy thn trng vi nhng cu hi. N s c hin th ti gc trn cng bn phi ca mn hnh</span>
                        </div>
                    </div>
                </div>

                <div className="col-sm-12 border_map_modal animate__zoomInDown animate__animated">
                    <div className="row" style={{ alignItems: 'center' }}>
                        <div className="col-sm-1">
                            <img
                                src={process.env.PUBLIC_URL + '/assets/images/time.png'}
                                style={{ width: '32px', height: '32px' }}
                            />
                        </div>
                        <div className="col-sm-11">
                            <span className='text_modal_map'>Ti gc trn cng bn tri mn hnh. y l ni hin th tng thi gian t ban u cho ti khi bn tm c kho bu.</span>
                        </div>
                    </div>
                </div>

                {itemModal.map(item => {
                    if (item?.effect.includes("back_forward")) {
                        item.des = "y l .....Khi bc vo y bn s c tin thm mt s  nht nh"
                    } else if (item?.effect.includes("forward")) {
                        item.des = "Khi bc vo y bn s phi li v sau"
                    } else if (item?.effect.includes("tele")) {
                        item.des = "Cng khng gian, bn s b dch chuyn ti mt ni bt k no  trong bn  khi bc vo n."
                    }

                    return (
                        <div className="col-sm-12 border_map_modal animate__zoomInDown animate__animated">
                            <div className="row" style={{ alignItems: 'center' }}>
                                <div className="col-sm-1">
                                    <Canvas keys={item?.td} img={srcImage[item?.tilesetIdx || 0]} />
                                </div>
                                <div className="col-sm-11">
                                    <span className='text_modal_map'>{item?.des}</span>
                                </div>
                            </div>

                        </div>
                    )
                })}

            </div>
        </div>
    );
}

export default GuideMap;